import React, { useEffect, useState } from 'react';
import Head from 'next/head';
import { useRouter } from 'next/router';
import { Briefcase, Plus, ArrowRight, LayoutGrid, Settings } from 'lucide-react';

export default function Applications() {
  const router = useRouter();
  const [content, setContent] = useState(null);

  // 1. Load Text Content
  useEffect(() => {
    fetch('/site-content.json')
      .then((res) => res.json())
      .then((data) => setContent(data));
  }, []);

  // 2. Security Check (Redirect if not logged in)
  useEffect(() => {
    const token = localStorage.getItem('token');
    if (!token) router.push('/');
  }, []);

  const handleAppSelect = (appName) => {
    // Save selection so Dashboard knows what to load
    localStorage.setItem('currentApp', appName);
    router.push('/dashboard');
  };

  if (!content) return <div className="min-h-screen bg-[#0B0B0F]" />; // Loading state

  return (
    <div className="min-h-screen bg-[#0B0B0F] text-white font-sans">
      <Head>
        <title>Select Application | {content.general.brandName}</title>
      </Head>

      {/* Header */}
      <header className="border-b border-white/5 bg-[#0B0B0F]/50 backdrop-blur-md sticky top-0 z-20">
        <div className="max-w-7xl mx-auto px-6 h-16 flex items-center justify-between">
          <div className="flex items-center gap-2 text-lg font-bold">
            <div className="w-8 h-8 rounded bg-gradient-to-br from-pink-600 to-purple-600 flex items-center justify-center">
              <LayoutGrid size={16} className="text-white" />
            </div>
            <span>{content.general.brandName}<span className="text-purple-400">{content.general.brandHighlight}</span></span>
          </div>
          <div className="w-8 h-8 rounded-full bg-white/10 border border-white/10"></div>
        </div>
      </header>

      <main className="max-w-7xl mx-auto px-6 py-12">
        <div className="mb-12">
          <h1 className="text-3xl md:text-4xl font-bold mb-2">
            {content.appsPage.title} <span className="bg-clip-text text-transparent bg-gradient-to-r from-pink-500 to-purple-500">Admin</span>
          </h1>
          <p className="text-gray-400">{content.appsPage.subtitle}</p>
        </div>

        {/* Grid */}
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
          
          {/* Portfolio App */}
          <div 
            onClick={() => handleAppSelect('portfolio')}
            className="group relative bg-[#121212] border border-white/5 rounded-2xl p-6 hover:border-purple-500/50 hover:shadow-[0_0_30px_-10px_rgba(168,85,247,0.3)] transition-all duration-300 cursor-pointer overflow-hidden"
          >
            <div className="absolute top-0 right-0 p-4 opacity-0 group-hover:opacity-100 transition-opacity">
              <ArrowRight className="text-purple-400 -rotate-45 group-hover:rotate-0 transition-transform duration-300" />
            </div>
            <div className="w-14 h-14 rounded-xl bg-gradient-to-br from-blue-600/20 to-purple-600/20 border border-white/5 flex items-center justify-center mb-6 group-hover:scale-110 transition-transform">
              <Briefcase className="text-purple-400 w-7 h-7" />
            </div>
            <h3 className="text-xl font-bold text-white mb-2">Portfolio CMS</h3>
            <p className="text-gray-400 text-sm mb-8">Manage personal projects, resume data, and contact submissions.</p>
            <div className="flex items-center gap-3 mt-auto">
              <span className="px-2 py-1 rounded bg-green-500/10 text-green-400 text-xs font-medium border border-green-500/20">‚óè Live</span>
            </div>
          </div>

          {/* Add New App */}
          <button className="flex flex-col items-center justify-center text-center p-6 rounded-2xl border border-dashed border-white/10 hover:border-pink-500/50 hover:bg-white/5 transition-all duration-300 h-full min-h-[250px] group">
            <div className="w-12 h-12 rounded-full bg-white/5 flex items-center justify-center mb-4 group-hover:bg-pink-600 transition-colors">
              <Plus className="text-gray-400 group-hover:text-white" />
            </div>
            <h3 className="text-lg font-medium text-white">{content.appsPage.createButton}</h3>
            <p className="text-gray-500 text-sm mt-1">{content.appsPage.createDesc}</p>
          </button>
        </div>
      </main>
      
      {/* Footer Reuse */}
      <footer className="border-t border-white/5 mt-auto py-8 text-center text-gray-500 text-sm">
        <p>{content.footer.copyright}</p>
      </footer>
    </div>
  );
}